.about-us-project {
    border-radius: 30px;
    transition: .3s;
    cursor: pointer;

    &:hover {
        transform: scale(1.1);
    }

    &__wrapper {
        margin: 60px 0 0 0;
        padding: 10px 15px;
    }

    &__number {
        display: flex;
    }

    &__icon {
        background-image: url("../img/web-programming.svg");
        background-repeat: no-repeat;
        background-position: center;
        width: 30px;
    }

    &__num {
        font-size: 36px;
        padding: 0 0 0 20px;
    }

    &__subtitle {
        font-size: 16px;
        line-height: 25px;
        color: #999999;
    }
}